export function attr(name, value) {
  return { type: "Attr", namespace: "", name: name, value: value };
}

export function attr_namespaced(namespace, name, value) {
  return { type: "Attr", namespace: namespace, name: name, value: value };
}

export function prop(name, value) {
  return { type: "Prop", name: name, value: value };
}

export function int_prop(name, value) {
  return { type: "Prop", name: name, value: any.from(value) };
}

export function float_prop(name, value) {
  return { type: "Prop", name: name, value: any.from(value) };
}

