"use strict";

const svg_namespace = "http://www.w3.org/2000/svg";

import * as element from "./playground/dom/element.js";

import * as event from "./playground/dom/event.js";

import * as svg from "./playground/dom/svg.js";

import * as program from "./playground/program.js";

import * as console from "./playground/util/console.js";

export function main() {
  let app = program.start(init(), draw, update);
  program.on_key_down(app, key_down);
  return program.on_key_up(app, key_up);
}

function init() {
  return {
    type: "Model",
    width: 600.0,
    height: 600.0,
    x_pos: 275.0,
    x_vel: 0.0,
    y_pos: 275.0,
    y_vel: 0.0
  };
}

function draw(model) {
  return svg.svg(
    [svg.width(model.width), [svg.height(model.height), []]],
    [
      svg.rect(
        [
          svg.width(50.0),
          [svg.height(50.0),
          [svg.x(model.x_pos),
          [svg.y(model.y_pos),
          [svg.fill("blue"),
          []]]]],
        ],
        [],
      ),
      [],
    ],
  );
}

function update(model) {
  let x_pos = clamp(model.x_pos + model.x_vel, 0.0, 600.0);
  let y_pos = clamp(model.y_pos + model.y_vel, 0.0, 600.0);
  return Object.assign({}, model, { x_pos: x_pos, y_pos: y_pos });
}

function key_down(model, event) {
  let $ = event.key;
  if ($ === "ArrowUp") {
    return Object.assign({}, model, { y_vel: -5.0 });
  } else if ($ === "ArrowDown") {
    return Object.assign({}, model, { y_vel: 5.0 });
  } else if ($ === "ArrowLeft") {
    return Object.assign({}, model, { x_vel: -5.0 });
  } else if ($ === "ArrowRight") {
    return Object.assign({}, model, { x_vel: 5.0 });
  } else {
    let other = $;
    return model;
  }
}

function key_up(model, event) {
  let $ = event.key;
  if ($ === "ArrowUp") {
    return Object.assign({}, model, { y_vel: 0.0 });
  } else if ($ === "ArrowDown") {
    return Object.assign({}, model, { y_vel: 0.0 });
  } else if ($ === "ArrowLeft") {
    return Object.assign({}, model, { x_vel: 0.0 });
  } else if ($ === "ArrowRight") {
    return Object.assign({}, model, { x_vel: 0.0 });
  } else {
    let other = $;
    return model;
  }
}

function clamp(val, min, max) {
  let $ = val < min;
  if ($) {
    return min;
  } else if (!$) {
    let $1 = val > max;
    if ($1) {
      return max;
    } else if (!$1) {
      return val;
    } else {
      throw new Error("Bad match");
    }
  } else {
    throw new Error("Bad match");
  }
}
