"use strict";

export const namespace = "http://www.w3.org/2000/svg";

import * as any from "../../playground/util/any.js";

import * as element from "../../playground/dom/element.js";

export function svg(attrs, children) {
  return element.element_namespaced(namespace, "svg", attrs, children);
}

export function rect(attrs, children) {
  return element.element_namespaced(namespace, "rect", attrs, children);
}

export function width(val) {
  return element.attr("width", any.to_string(val));
}

export function height(val) {
  return element.attr("height", any.to_string(val));
}

export function x(val) {
  return element.attr("x", any.to_string(val));
}

export function y(val) {
  return element.attr("y", any.to_string(val));
}

export function fill(val) {
  return element.attr("fill", val);
}
