function tick(program) {
  return () => {
    element.render_into(program.draw(ref.get(program.model)), program.root);
    ref.update(program.model, program.update);
    let id = request_animation_frame(tick(program));
    return ref.set(program.id, id);
  };
}

function cancel_animation_frame(id) {
  return window.cancelAnimationFrame(id)
}

export function stop(program) {
  cancel_animation_frame(ref.get(program.id));
  ref.set(program.id, 0);
  return program;
}

